import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/view/layout'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
  	  //
	  {
	    path: '',
	    component: layout,
	    children: [
	      { path: '', name: '',component: resolve => require(['@/view/index/index.vue'], resolve) },
	      { path: '/announced', name: '',component: resolve => require(['@/view/index/announced.vue'], resolve) },
	      { path: '/thesun', name: '',component: resolve => require(['@/view/index/the_sun.vue'], resolve) },
	      { path: '/shopcar', name: '',component: resolve => require(['@/view/index/shop_car.vue'], resolve) },
	      { path: '/user', name: '',component: resolve => require(['@/view/index/user.vue'], resolve) }
	    ]
	  },
	  //
	  {
	    path: '/user',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/user/detail', name: '',component: resolve => require(['@/view/user/detail.vue'], resolve) },
	      { path: '/user/set', name: '',component: resolve => require(['@/view/user/personal_set.vue'], resolve) },
	      { path: '/user/updataphoto', name: '',component: resolve => require(['@/view/user/my_photo.vue'], resolve) },
	      { path: '/user/updatanickname', name: '',component: resolve => require(['@/view/user/my_nickname.vue'], resolve) },
	      { path: '/user/myaddress', name: '',component: resolve => require(['@/view/user/my_address.vue'], resolve) },
	      { path: '/user/myaddressadd', name: '',component: resolve => require(['@/view/user/my_address_add.vue'], resolve) },
	      { path: '/user/recharge', name: '',component: resolve => require(['@/view/user/recharge.vue'], resolve) },
	      { path: '/user/withdrawals', name: '',component: resolve => require(['@/view/user/withdrawals.vue'], resolve) }
	    ]
	  },
	  //
	  {
	    path: '/order',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/order/myorder', name: '',component: resolve => require(['@/view/order/my_order.vue'], resolve) }
	    ]
	  },
	  //
	  {
	    path: '/winning',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/winning/mywinning', name: '',component: resolve => require(['@/view/winning/my_winning.vue'], resolve) },
	      { path: '/winning/detail', name: '',component: resolve => require(['@/view/winning/detail.vue'], resolve) }
	    ]
	  },
	  //
	  {
	    path: '/thesun',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/thesun/detail', name: '',component: resolve => require(['@/view/thesun/detail.vue'], resolve) },
	      { path: '/thesun/mythesun', name: '',component: resolve => require(['@/view/thesun/my_the_sun.vue'], resolve) },
	      { path: '/thesun/share', name: '',component: resolve => require(['@/view/thesun/input.vue'], resolve) }
	    ]
	  },
	  //APP
	  {
	    path: '/system',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/system/help', name: '',component: resolve => require(['@/view/system/help.vue'], resolve) },
	      { path: '/system/rule', name: '',component: resolve => require(['@/view/system/rule.vue'], resolve) },
	      { path: '/system/question', name: '',component: resolve => require(['@/view/system/question.vue'], resolve) },
	      { path: '/system/agreement', name: '',component: resolve => require(['@/view/system/agreement.vue'], resolve) },
	      { path: '/system/about', name: '',component: resolve => require(['@/view/system/about.vue'], resolve) }
	    ]   
	  },
	  //
	  {
	    path: '/goods',
	    component: resolve => require(['@/view/public/child_header.vue'], resolve),
	    children: [
	      { path: '/goods/classification', name: '',component: resolve => require(['@/view/goods/classification.vue'], resolve) },
	      { path: '/goods/detail', name: '',component: resolve => require(['@/view/goods/detail.vue'], resolve) },
	      { path: '/goods/fastopen', name: '',component: resolve => require(['@/view/goods/fast_open.vue'], resolve) },
	 	  { path: '/goods/classificationdetail', name: '',component: resolve => require(['@/view/goods/classification_detail.vue'], resolve) }
	    ]   
	  },	
	  //
	  {
	  	path: '/service',name: '',component: resolve => require(['@/view/common/service.vue'], resolve) 
	  },
	  {
	  	path: '/notice',name: '',component: resolve => require(['@/view/common/notice.vue'], resolve) 
	  },
	  {
	  	path: '/systemset',name: '',component: resolve => require(['@/view/common/system_set.vue'], resolve) 
	  },
	  {
	  	path: '/login',name: '',component: resolve => require(['@/view/login/login.vue'], resolve) 
	  },
	  {
	  	path: '/register',name: '',component: resolve => require(['@/view/login/register.vue'], resolve) 
	  }	  
  ]
})
 


